document.addEventListener('DOMContentLoaded', function(){

    crearGaleria();
});

function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes');

    for( let i = 1 ; i<=12 ; i++){
        const imagen =  document.createElement('IMG');
        imagen.src = `build/img/thumb/${i}.webp`;
        imagen.alt = `imagen numero ${i}`

        imagen.dataset.imagenID = i;

        // añadir la funcion mostrar imagen
        imagen.onclick = mostrarImagen;

        const lista = document.createElement('LI');
        lista.appendChild(imagen);
        galeria.appendChild(lista);
    }
}

function mostrarImagen(e) {

    let id = parseInt(e.target.dataset.imagenID);
    
   

    const overlay = document.createElement('DIV');
    overlay.classList.add('overlay');

    // cuando se da click, cerrar la imagen 
    // overlay.onclick = function(){
    //     overlay.remove();
    // }

    // Generar la imagen 
    const imagen = document.createElement('IMG');
    imagen.src = `build/img/grande/${id}.webp`;
    overlay.appendChild(imagen)

    //cuando se haga click , cambiar la imagen 

    

    //añadir el boton para cerrar la imagen 
    const boton = document.createElement('P');
    boton.textContent = 'X';
    boton.classList.add('btn-cerrar');

    // cuando se preciona , Cierra la imagen
    boton.onclick = function(){
        overlay.remove();
        body.classList.remove('fijar-body')
    }


    const botonNext = document.createElement('P');
    botonNext.textContent = '->';
    botonNext.classList.add('btn-movimiento');
    botonNext.classList.add('derecha');

    botonNext.onclick = function(){
        let i = id;
        if(i < 12) {
            i++;
            id++;
        }
        else {
            i=1;
            id = 1;     
        }
        imagen.src = `build/img/grande/${i}.webp`;
    };

    const botonPre = document.createElement('P');
    botonPre.textContent = '<-';
    botonPre.classList.add('btn-movimiento');
    botonPre.classList.add('izquierda');

    botonPre.onclick = function(){
        let i = id;
        if(i == 1) {
            i=12;
            id=12;
        }
        else {
            i--;
            id --;     
        }
        imagen.src = `build/img/grande/${i}.webp`;
    };
    
    
    overlay.appendChild(boton);
    overlay.appendChild(botonNext);
    overlay.appendChild(botonPre);
    //mostrar en el html 
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body')
}

function cambiarImagen(imagen,id) {
    let i = id;
    console.log('imagen cambiada')
    if(i<12){
        i++
    }
    else{
        i=1
    }
    imagen.src = `build/img/grande/${i}.webp`;
    
}

